<template>
<div class="container">
	<div class="card">
		<h1>{{ count }}</h1>
			<button @click="addVal"> INCREASE</button>
			<button @click="remVal"> DECREASE</button>
		</div>
</div>
</template>


<script>
export default {
  name: 'app',
  data: () => ({
	count:0
}),
methods: {
	remVal(){
		if(this.count>0){
			this.count--;
		}

	},
	addVal(){
		if(this.count<5){
			this.count++;
		}

	},
	inc(){
		if(this.count>0){
			this.count++;
		}
	}
  }
}
</script>

